@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@600;700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Poppins', sans-serif;
}

ul, ol {
    list-style: none;
}

button {
    background: none;
    border: none;

    &:hover {
        cursor: pointer;
    }
}

a {
    cursor: pointer;
    color: $link-color;
}

input {
    border: none;
    background: transparent;
}

